import React,{useState,useContext,useReducer} from "react"
import sublinks from "./data"
import cartItems from "./StoreData"



export const AppContext=React.createContext()


export const AppProvider=({children})=>{
    const [isSidebarOpen,setIsSidebarOpen]=useState(false)
    const [isSubmenuOpen,setIsSubmenuOpen]=useState(false)
    const [isPurchaseDialog,setIsPurchaseDialog]=useState(false)
    const [location,setLocation]=useState({})
    const [page,setPage]=useState({page:"",links:[]})
    const [searchTerm, setSearchTerm] = useState('a')
    const [state,dispatch]=useReducer(cartItems)

    const openSidebar=()=>{
     setIsSidebarOpen(true)
    }

    const closeSidebar=()=>{
     setIsSidebarOpen(false)
    }
    
    const openSubmenu=(text,coordinates)=>{   
     const page=sublinks.find((link)=>link.page===text)
     setPage(page)
     setLocation(coordinates)
     setIsSubmenuOpen(true)
    }

    const closeSubmenu=()=>{
     setIsSubmenuOpen(false)
    }

    const openPurchaseDialog=()=>{
        setIsPurchaseDialog(true)
    }
    const closePurchaseDialog=()=>{
        setIsPurchaseDialog(false)
    }

    return <AppContext.Provider  value={{
        openPurchaseDialog,closePurchaseDialog,isPurchaseDialog,searchTerm,setSearchTerm,location,page,isSubmenuOpen,isSidebarOpen,openSidebar,closeSidebar,openSubmenu,closeSubmenu}}>
        {children}</AppContext.Provider>
}

export const useGlobalContext=()=>{
       return useContext(AppContext)
}